<template>
  <ul>
    <li
      v-bind:class="{ focus: item.id === navid }"
      v-for="(item, index) in list"
      :key="index"
      @click="change(item.id)"
    >
      {{ item.name }}
    </li>
  </ul>
</template>

<script>
export default {
  name: "Menu",
  props: {

  },
  methods: {
    change(num) {
      this.navid = num;
	  this.deliver();
    },
	deliver(){
		this.$emit('deliver',this.navid)
	}
  },
  data: function() {
    return {
      navid: -1,
      list: [
        {
          name: "Python",
          id: 0
        },
        {
          name: "PHP",
          id: 1
        },
        {
          name: "Java",
          id: 2
        },
        {
          name: "C#",
          id: 3
        },
        {
          name: "GO",
          id: 4
        },
        {
          name: "Html",
          id: 5
        }
      ]
    };
  },

};
</script>

<style>
ul {
  width: 230px;
}
ul li {
  list-style: none;
  color: #324a60;
  background-color: #001f3a;
  padding: 20px;
  width: 150px;
  text-align: center;
  font-size: 20px;
  margin-left: 20px;
}
ul li:hover {
  cursor: pointer;
  font-size: 25px;
  color: #09e7fc;
  width: 170px;
  border-bottom-right-radius: 10px;
  border-top-right-radius: 10px;
  padding-left: 30px;
}

.focus {
  font-size: 25px;
  color: #09e7fc;
  background-color: #003f73;
}
</style>
